.black-nav{
  width:100%;
  padding-left:20px;
  background-color: #333;
  color:#fff;
  display:flex;
}

div{
  box-sizing: border-box;
}

.list{
  padding:0 0 20px 20px;
  border-bottom:1px solid gray;
  text-align: left;
}

.modal{
  width:80%;
  height:50%;
  padding:30px;
  background-color:#eee;
  text-align: left;
  position:absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
}

/* 텍스트 드래그, 파란 블럭 안 되게 하기 */
body {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position:relative;
}

.titlename{
  cursor:pointer;
}

.sortbtn{
  margin-left:50%;
  margin-top:15px;
  padding:5px;
  border-radius:10px; 
}
